import React from "react";
import "./index.css";

const StartHomeShare = () => {
  const sharing = [
    {
      name: "Shannon Lawton",
      share:
        "Ti  rt chc chn v ngi n ng tip theo ti  bn mt cch nghim tc, s l ngi ti dnh trn phn i  bn (ch l khng kt hn). Ti  rt kn chn v khng mong gp mt ngi n ng trn Tinder. Nhng ti vn quyt nh ng k ri c tng hp xem sao.",
    },
    {
      name: "Sean & Marianna",
      share:
        'Ti va chm dt mt mi quan h cn Annie va bt u hn h vi ph n v vy c hai chng ti hi nht v  phng, d dt tin tng bc! C hai u  "" rt nhanh v nhn ra l mnh  tm c mnh ghp cn li.',
    },
    {
      name: "Kenneth ",
      share:
        "CM N TINDER v  gip ti gp c tri k ca mnh. Ch nm pht sau khi bt u ni chuyn, c y, ngi hin l b x ca ti  ni v chuyn chng ti s c mt m ci tuyt th no.",
    },
  ];

  return (
    <div className="start-home__share-wrapper">
      {sharing.map((share, index) => {
        return (
          <div className="sharing-item" key={index}>
            <div className="header">
              <div className="text">
                <h2 className="sharing-item__name">{share.name}</h2>
              </div>
              <div className="icon">
                <i className="fa-solid fa-quote-left"></i>
              </div>
            </div>
            <div className="body">
              <p className="sharing-item__paragraph">{share.share}</p>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default StartHomeShare;
