import React from "react";
import "./index.css";
import MyWallAside from "../../components/MyWallAside";
import { Route, Routes } from "react-router-dom";
import MyProfile from "../../components/MyProfile";
import MyWallFeature from "../../components/MyWallFeature";
import SettingDetail from "../../components/SettingDetail";
import MyWallMain from "../../components/MyWallMain";
import MatchesCard from "../../components/MatchesCard";
import ProfileCard from "../../components/ProfileCard";
import EditProfileCard from "../../components/EditProfileCard";
import ChoicesCard from "../../components/ChoicesCard";

const MyWall = () => {
  return (
    <div className="mywall__wrapper">
      <Routes>
        <Route path="" element={<MyWallAside />}>
          <Route path="recs" element={<MyWallFeature />} />
          <Route path="profile/*" element={<MyProfile />}>
            {/* <Route index element={<MyProfile />} />
            <Route path="edit" element={<MyProfile />} />
            <Route path="interests" element={<MyProfile />} /> */}
          </Route>
          <Route path="settings">
            <Route
              path="phone-number"
              element={
                <SettingDetail
                  type="text"
                  item={{ label: "phone number", content: "84369669415" }}
                />
              }
            />
            <Route
              path="email-account"
              element={
                <SettingDetail
                  type="text"
                  onlyRead={true}
                  item={{ label: "Email", content: "minhkhoi.bemap@gmail.com" }}
                />
              }
            />
            <Route
              path="gender"
              element={
                <SettingDetail
                  type="choose"
                  item={["men", "women", "everyone"]}
                />
              }
            />
          </Route>
        </Route>
      </Routes>

      <Routes>
        <Route path="" element={<MyWallMain />}>
          <Route path="recs" element={<MatchesCard />} />
          <Route path="profile/*">
            <Route index element={<ProfileCard />} />
            <Route path="edit" element={<EditProfileCard />} />
            <Route
              path="edit/interests"
              element={
                <ChoicesCard
                  headerTitle="Sa s thch"
                  note="Chn cc s thch bn mun chia s vi mi ngi. Chn ti thiu 3."
                  label="S thch"
                  maxSelectTag={5}
                  tagsArray={[
                    "Th h 9X",
                    "Harry Potter",
                    "SounCloud",
                    "Spa",
                    "Chm sc bn thn",
                    "Tic gia nh",
                    "Bng r",
                    "Du lch",
                    "Lm vic t do",
                    "Pht trin bn thn",
                    "u t",
                    "Pht",
                  ]}
                />
              }
            />
            <Route
              path="edit/zodiac"
              element={
                <ChoicesCard
                  headerTitle="Sa cung hong o"
                  label="Cung hong o"
                  note="Cung hong o ca bn l g?"
                  maxSelectTag={1}
                  tagsArray={[
                    "Ma Kt",
                    "Bo Bnh",
                    "Song Ng",
                    "Bch Dng",
                    "Kim Ngu",
                    "Song T",
                    "C Gii",
                    "S T",
                    "X N",
                    "Thin Bnh",
                    "B Cp",
                    "Nhn M",
                  ]}
                />
              }
            />
            <Route
              path="edit/education"
              element={
                <ChoicesCard
                  headerTitle="Sa trnh  hc vn"
                  label="Hc vn"
                  note="Trnh  Hc vn ca bn l?"
                  maxSelectTag={1}
                  tagsArray={[
                    "Trung hc ph thng",
                    "C nhn",
                    "tin s",
                    "thc s",
                    "gio s",
                  ]}
                />
              }
            />
            <Route
              path="edit/pets"
              element={
                <ChoicesCard
                  headerTitle="Sa S Thch Th Cng"
                  label="Loi th cng"
                  note="Loi th cng bn thch l?"
                  maxSelectTag={1}
                  tagsArray={[
                    "Ch",
                    "mo",
                    "b st",
                    "ng vt lng c",
                    "C",
                    "Khng nui th cng",
                    "Tt c cc loi th cng",
                  ]}
                />
              }
            />
            <Route
              path="edit/smoking"
              element={
                <ChoicesCard
                  headerTitle="Sa thi quen ht thuc"
                  label="Thi quen ht thuc"
                  note="Thi quen ht thuc ca bn?"
                  maxSelectTag={1}
                  tagsArray={[
                    "H thuc vi bn b",
                    "Ht thuc thng xuyn",
                    "Khng ht thuc",
                  ]}
                />
              }
            />
            <Route
              path="edit/gender"
              element={
                <ChoicesCard
                  headerTitle="Sa Gii tnh"
                  note="Gii tnh ca bn l g?"
                  label="Gii tnh"
                  maxSelectTag={1}
                  tagsArray={["nam", "n", "khc"]}
                />
              }
            />
          </Route>
          <Route path="settings/*" element={<ProfileCard />} />
        </Route>
      </Routes>
    </div>
  );
};

export default MyWall;
